import { Component, OnInit } from '@angular/core';
import { SignalRService } from 'src/app/services/chat.service';
import { LoginService } from './../../services/login.service'

@Component({
  selector: 'app-logout',
  template: ``,
  styles: []
})
export class LogoutComponent implements OnInit {

  constructor(private loginService: LoginService, private signalRService: SignalRService) { }

  ngOnInit() {
    var obj = {
      customerID: localStorage['customerID'],
      userID: localStorage['userID']
    }
    this.loginService.logout(obj).subscribe((res) => {
      this.signalRService.removeFromGroup()
      this.loginService.redirect();
    }, (err) => {
      console.log(err)
    })
  }

}
